	module.exports = {"data":{"wordpressPost":{"title":"The super easy nail tips we swear by","content":"<p>Winter can be awesome and for a whole raft of reasons. Right now we can&#8217;t think of any though so um &#8230; get back to us on that. The chilly season can suck for a bunch of reasons too &#8211; and we know all about those.</p>\n<p>It wreaks havoc on skin and it&#8217;s not polite to nails either. But it&#8217;s okay. Better even. Because Tracey Winder, <strong>nail genius and trainer at Mavala</strong> can fix your nails in a flash. We asked her how and this is what she told us. Frankly, our fingertips have never looked prettier.</p>\n<p><strong>Firstly, what effect does winter have on hands and nails?</strong><br />\nNail growth slows down during the winter months when a drop in temperatures coincides with a reduction in circulation. Blood flow to the matrix (the root of the nail) is compromised, the engine room for the healthy production and growth of nail tissue.<strong> As environmental conditions change with less moisture in the air</strong>, nails are affected just as the skin is and the nail plates become more dehydrated. Not to mention, switching between temperature extremes of warm heating indoors and cool condition outdoors.</p>\n<p><strong>What are the symptoms with hands and nails?</strong><br />\nSkin on the hands is susceptible to becoming red, dry and chapped as the moisture barrier is compromised during winter. Nail condition is weakened, so you will likely notice <strong>nails becoming dryer, being more brittle,</strong> softer or even peeling. Cracked, dry cuticles are often a result of a change in weather, which not only look unsightly but can be painful too! All of these conditions affect what your manicure looks like and how long it lasts.</p>\n<img class=\"alignnone size-medium wp-image-1295\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213246/270-Firenze-149x300.jpg\" alt=\"\" width=\"149\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213246/270-Firenze-149x300.jpg 149w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213246/270-Firenze-768x1544.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213246/270-Firenze-509x1024.jpg 509w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213246/270-Firenze.jpg 800w\" sizes=\"(max-width: 149px) 100vw, 149px\" /> <img class=\"alignnone size-medium wp-image-1296\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213305/266-Singapore-149x300.jpg\" alt=\"\" width=\"149\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213305/266-Singapore-149x300.jpg 149w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213305/266-Singapore-768x1544.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213305/266-Singapore-509x1024.jpg 509w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213305/266-Singapore.jpg 800w\" sizes=\"(max-width: 149px) 100vw, 149px\" /> <img class=\"alignnone size-medium wp-image-1297\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213324/237-Basel-149x300.jpg\" alt=\"\" width=\"149\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213324/237-Basel-149x300.jpg 149w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213324/237-Basel-768x1544.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213324/237-Basel-509x1024.jpg 509w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/27213324/237-Basel.jpg 800w\" sizes=\"(max-width: 149px) 100vw, 149px\" />\n<p><em>Looking for a quality polish that&#8217;s super pretty and protects nails too? Try <a href=\"http://mavala.com.au\">Mavala Nail Polish</a> in Firenze, Singapore or Basel.</em></p>\n<p><strong>What special care is required during this time of year?</strong><br />\nLike with your skin, this is the time of year to be introducing some additional steps into your hand and nail care routine to compensate for the winter aggressors. <strong>A little bit of TLC with your nails will go a long way</strong> in protecting your manicure! Your hands and nails need to be ‘nursed’ back to health, so be sure to apply specific rich, nutritive treatment products. Always protect the nails with a coat of polish that is good for the nails, i.e. a product that contains ingredients that wont compromise the health of the nail (so at a minimum 5-free) and will allow the nail to breathe. This doesn’t always have to be colour, even a natural clear top or base coat will protect during winter.</p>\n<p><strong>Any other tips for protecting nails during winter?</strong><br />\nKeep your nails shorter during winter, with regular filing. Always use a gentle, acetone-free remover, which is much less drying on the nail. <strong>Take care to wear gloves while using water and detergent.</strong> Drink lots of water to compensate for the extra dehydration and ensure your diet is rich in vitamins (A,B,C,D and E) and minerals (calcium, iron, silicium) to ‘feed’ healthy nail growth. Nail-loving supplements are also an option during winter months.</p>\n<img class=\"alignnone size-medium wp-image-566\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155531/brilliant-strength-nail-enamel-651851-136x300.jpg\" alt=\"\" width=\"136\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155531/brilliant-strength-nail-enamel-651851-136x300.jpg 136w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155531/brilliant-strength-nail-enamel-651851-768x1694.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155531/brilliant-strength-nail-enamel-651851-464x1024.jpg 464w\" sizes=\"(max-width: 136px) 100vw, 136px\" /><img class=\"alignnone size-medium wp-image-575\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155955/Sally-Hansen3D-Gel-Shine-21-141x300.jpg\" alt=\"\" width=\"141\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155955/Sally-Hansen3D-Gel-Shine-21-141x300.jpg 141w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155955/Sally-Hansen3D-Gel-Shine-21-480x1024.jpg 480w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155955/Sally-Hansen3D-Gel-Shine-21.jpg 571w\" sizes=\"(max-width: 141px) 100vw, 141px\" /><img class=\"alignnone size-medium wp-image-563\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155345/CrystalNailFile1-300x163.jpg\" alt=\"\" width=\"300\" height=\"163\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155345/CrystalNailFile1-300x163.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155345/CrystalNailFile1-768x418.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13155345/CrystalNailFile1-1024x558.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><em>Try a protective product such as <a href=\"http://revlonanz.com\">Revlon Brilliant Strength</a> or <a href=\"http://ausallyhansen.com\">Sally Hansen Gel Shine 3D Top Coat </a>and always use a quality nail file such as this crystal version by <a href=\"http://modelcocosmetics.com\">ModelCo</a> Crystal Nail and in one direction only please. One Direction. Snigger.</em></p>\n<p>&nbsp;</p>\n<p><strong>What You Need &#8230;</strong><br />\n<strong>• Gloves</strong> &#8211; an essential item for protection when hands and nails are immersed in water.<br />\n<strong>• Hand cream</strong> &#8211; applied frequently through the day, so a hand-bag friendly option is a must<br />\n<strong>• Cuticle oil</strong> &#8211; best applied after a hot shower at night for best absorption<br />\n<strong>• Nail strengthener</strong> &#8211; look for a product that helps to speed up and stimulate nail growth, whilst hydrating at the same time.<br />\n<strong>• Nail nutritive treatment</strong> &#8211; overnight is the best time to treat nails and hands. Apply a rich product and sleep in cotton gloves to increase the intensity and effectiveness of the treatment.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/27131433/NicM_1328_R2.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"14c821f2-e568-5e99-aff7-91947d1a2763"}}

